#signInButton,
#signOutButton,
#home,
#loader-page {
  text-align: center;
  background-color: #85ffbd;
  background-image: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
  height: 100vh;
  padding: 0rem 0.5rem;
}

#home {
  text-align: left;
  padding: 0.5rem;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader > img {
  width: 40px;
}

#signInButton h1,
#signOutButton h1 {
  padding: 2rem;
}

#home td {
  font-size: 1.2rem;
}

#home textarea {
  width: 100%;
  font-size: 1rem;
  outline: none;
  padding: 0.2rem;
  border-radius: 4px;
  border: 1px solid black;
}

#home button {
  width: 100%;
  font-size: 1rem;
  padding: 0.2rem 1rem;
  background-color: #414141;
  color: #f1f1f1;
  font-weight: bold;
  margin: 0.2rem 0rem;
  border-radius: 4px;
}
